import React, { useEffect, useState } from "react";

const Textview = React.memo(({text}) => {
    useEffect(()=>{
        console.log("TextView ");
    })
    return <div>{text}</div>
})

/*React.memo()  () ,   parameter(props)   React.memo  . */

const areEqual = (prevProps, nextProps)=>{
    if(prevProps.obj.count === nextProps.obj.count)return true;
    
    return false;
}

/* React.memo   .   parameter  ,  ,  

   ,    props setState          .

, React.memo props             .
props  string number  ,    props      new  ,           .

 areEqual ,      React.memo   ,    props  React.memo  props       .
*/

const Countview = React.memo(({count}) => {
    useEffect(()=>{
        console.log("CountView ");
    })
    return <div>{count}</div>
})



const OptimizeTest = () =>{
    const [count,setCount] = useState(1);
    const[text,setText] = useState("");
    const [obj, setObj] =useState({
        count:1,
    }) //     

    return(
        <div style={{padding: 50}}> 
            <div>
                <h2>Count</h2>
                <Countview count={count}></Countview>
                <button onClick={()=>{setCount(count+1)}}>+</button>
            </div>
            <div>
                <h2>Text</h2>
                <Textview text = {text}></Textview>
                <input value={text} onChange={(e)=>{setText(e.target.value)}}/>
            </div>
        </div>
    ) 
}

export default OptimizeTest;

