import React,{ useContext, useRef, useState } from "react"
import ReactLinkify from "react-linkify";
import { DiaryDispatchContext } from "./App";

const DiaryItem = ({author, content, created_date, emotion,id}) => {
    const {onRemove,onEdit} = useContext(DiaryDispatchContext);
    const handleRemove = () => {
        if(window.confirm(`${id}    ?`)){onRemove(id)};
    }

    const[localContent, setLocalContent] = useState(content);

    const[isEdit, setIsEdit] = useState(false);
    const toggleIsEdit = () => {
        setIsEdit(!isEdit);
    }

    const handleQuitEdit = () => {
        setIsEdit(false);
        setLocalContent(content);
    }

    const localContentInput = useRef();
    const handleEdit = () => {
        
        if(localContent.length < 5){
            localContentInput.current.focus();
            return;
        }

        if(window.confirm(`${id}    ?`)){
            onEdit(id, localContent);
            toggleIsEdit();
        }
    }


    return <div className="DiaryItem">
        <div className="info">
            <span>: {author} | : {emotion}</span>
            <br></br>
            <span className="date">{new Date(created_date).toLocaleString()}</span>
        </div>
        <div className="content">
            <ReactLinkify>
            {isEdit ? <>
                <textarea ref={localContentInput} value={localContent} onChange={(e)=>setLocalContent(e.target.value)}></textarea>
            </> : <>{content}</>}
            </ReactLinkify>
        </div>
        {isEdit ? <>
            <button onClick={handleEdit}></button>
            <button onClick={handleQuitEdit}></button>
        </> : <>
            <button onClick={handleRemove}></button>
            <button onClick={toggleIsEdit}></button>
        </>
        }
        
    </div>
}

export default React.memo(DiaryItem);